















import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';
import { ACTION_ADD_OPERATIONS } from '@/store';
import operation from '@/components/Operation.vue';

@Component({
  components: {
    operation,
  },
})
export default class Plus extends Vue {
  public operation: string = '';
  public numbers: string = '';
  public max: string = '';

  public get loaded(): boolean {
    return this.$store.state.operations.length > 0;
  }

  public get hasOperation(): boolean {
    return this.$store.state.current > -1;
  }

  public get ops(): any[] {
    // return [];
    return this.$store.state.operations;
  }

  private async getPlusTest(): Promise<void> {
    this.operation = '+';
    return await this.getTest(
      'plus',
      `query Plus($numbers: String, $max: Int!) {
  plus(numbers: $numbers, max: $max) {
    first, second, response
  }
}`,
      {
          numbers: this.numbers,
          max: Number.parseInt(this.max, 10),
      });
  }

  private async getMinusTest(): Promise<void> {
    this.operation = '-';
    return await this.getTest(
      'minus',
      `query Minus($numbers: String) {
  minus(numbers: $numbers) {
    first, second, response
  }
}`,
    {
        numbers: this.numbers,
    });
  }

  private async getMultTest(): Promise<void> {
    this.operation = 'x';
    return await this.getTest(
      'mult',
      `query Mult($numbers: String, $max: Int) {
  mult(numbers:$numbers, max: $max) {
    first, second, response
  }
}`,
        {
          numbers: this.numbers,
          max: Number.parseInt(this.max, 10),
        });
  }

  private async getDivTest(): Promise<void> {
    this.operation = 'รท';
    return await this.getTest(
      'div',
      `query Div($numbers: String) {
  div(numbers: $numbers) {
    first, second, response
  }
}`,
    {
        numbers: this.numbers,
    });
  }

  private async getTest(op: string, q: string, v?: any): Promise<void> {
    try {
      const res = await axios.post(
        'https://simardo-hello-world-2.azurewebsites.net/graphql',
        {
          query: q,
          variables: v !== undefined ? v : undefined,
        },
      );
      console.log(res.data.data[op]);
      this.$store.dispatch(ACTION_ADD_OPERATIONS, res.data.data[op]);
    } catch (e) {
      console.log('err', e);
    }
  }

  private getNext(): void {
    // this.$store.commit(MUTATION_NEXT_OPERATION);
  }
}
