import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const ACTION_ADD_OPERATIONS: string = 'addOperationsAction';
export const ACTION_ADD_RESULT: string = 'addResultAction';

const MUTATION_NEXT_OPERATION: string = 'nextOperationsMutation';
const MUTATION_ADD_OPERATIONS: string = 'addOperationsMutation';
const MUTATION_ADD_RESULT: string = 'addResultMutation';

export interface OperationResult {
    index: number;
    response: number;
}

export interface SCoolState {
    operations: any[];
    current: number;
    results: OperationResult[];
}

export default new Vuex.Store<SCoolState>({
    strict: true,
    state: {
        operations: [],
        current: -1,
        results: [],
    },
    mutations: {
        [MUTATION_ADD_OPERATIONS](state: SCoolState, payload: any) {
            state.current = -1;
            state.operations = payload;
        },
        [MUTATION_NEXT_OPERATION](state: SCoolState) {
            state.current = Math.floor(Math.random() * state.operations.length);
        },
        [MUTATION_ADD_RESULT](state: SCoolState, payload: any) {
            state.results.push(payload);
        },
    },
    actions: {
        [ACTION_ADD_OPERATIONS](context: any, payload: any) {
            context.commit(MUTATION_ADD_OPERATIONS, payload);
            context.commit(MUTATION_NEXT_OPERATION);
        },
        [ACTION_ADD_RESULT](context: any, payload: any) {
            const result: OperationResult = {
                index: context.state.current,
                response: payload,
            };
            context.commit(MUTATION_ADD_RESULT, result);
            context.commit(MUTATION_NEXT_OPERATION);
        },
    },
    getters: {
        currentOperation: (state: SCoolState) => {
            return state.current === -1 ? undefined : state.operations[state.current];
        },
    },
});
