




import { Component, Vue, Prop } from 'vue-property-decorator';
import { OperationResult } from '@/store';

@Component
export default class Result extends Vue {
    @Prop()
    public value!: OperationResult;

    @Prop()
    public sign!: string;

    public get isSuccess(): boolean {
        return this.value.response === this.operation.response;
    }

    public get operation(): any {
        return this.$store.state.operations[this.value.index];
    }
}
