






















import { Component, Vue, Prop } from 'vue-property-decorator';
import { ACTION_ADD_RESULT } from '@/store';
import result from '@/components/Result.vue';

@Component({
  components: {
    result,
  },
})
export default class Operation extends Vue {
    @Prop()
    public sign!: string;

    public response: string = '';

    public get operation(): any | undefined {
        return this.$store.getters.currentOperation;
    }

    public get results(): any | undefined {
        return this.$store.state.results.slice().reverse();
    }

    public validate(): void {
        this.$store.dispatch(ACTION_ADD_RESULT, Number.parseInt(this.response));
        this.response = '';
    }
}
